@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}


body {
  background-color: #08395B;
  font-size: 16px;
  font-family: 'Open Sans', sans-serif;
}


.main-container {
  display: flex;
  flex-direction: row;
  justify-items: center;
}

/* logo container styles */

.logo-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 252px;
  padding-right: 252px;
  
}

.logo-container h3 {
  color: rgb(242, 242, 242);
  text-align: center;
  font-size: 45px;
  padding-top: 52px;
  padding-bottom: 160px;
}

.logo {
  width: 457.5px;
  padding-bottom: 258px;
}

.icon-prompt {
  padding-right: 0.5em;
}

.logo-container a {
  text-decoration: none;
  color: #ffffff;
  font-weight: 700;
  line-height: 1.5px;
  font-size: 22px;
}

/* globe image styles */
.globe img {
  padding-top: 77.52px;
  padding-right: 28.82px;
  padding-bottom: 28.82px;
  max-width: 905.26px;
  height: auto;
}

/* video bar styles */

.video-bar {
  width: 1920px;
  height: 58px;
  background-color: #C4C4C4;
  text-align: center;
  padding-top: 22px;
}